.loginContainer {
  height: 70vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 0 auto;
  max-width: 350px;
  width: 100%;
  padding: 30px;
}
.loginContainer > h1 {
  text-align: center;
  margin: 0 0 20px 0;
}
.loginContainer p {
  margin: 5px;
}
.formContainer {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.formSummaryError {
  border: 1px solid red;
  padding: 5px;
}
.button {
  content: none;
  background: none;
  border: none;
  margin: 5px 0 0 0;
  padding: 0;
}
.myButton {
  width: 100%;
}
